import commonView from '@/components';
import defaultLog from "@/components/Log/defaultLog.md";
import log4j from "@/components/Log/log4j.md";
import logLevel from "@/components/Log/logLevel.md";

export default{
    path:'/log',
    cnName:'',
    component:commonView,
    name:"log",
    children:[
        {
            path:'defaultLog',
            component:defaultLog,
            cnName:'',
            title:'Spring Boot',
            date:'2018-11-21',
            name:'defaultLog'
        },
        {
            path:"log4j",
            component:log4j,
            cnName:"log4j",
            title:'Spring bootlog4j',
            date:'2018-11-22',
            name:'log4j'
        },
        {
            path:"logLevel",
            component:logLevel,
            cnName:"",
            title:"Spring Bootlog4j",
            date:'2018-11-23',
            name:"logLevel"
        }
    ]
}


