<template>
    <div class="first">
        <h2>HelloWorld</h2>
        <tabs>
            <tab-pane label="" icon="md-code">
                <div class="tab-item">
                    <h3></h3>
                    <div class="img-box">
                        <img src="../../assets/helloworld.png" />
                    </div>
                </div>
            </tab-pane>
            <tab-pane label="Web" icon="md-code">
                <div class="tab-item">
                  <h3>Web</h3>
                  <div class="context-box">
                    <p><span class="code">pom.xml</span></p>
                    <ul>
                      <li>
                        <span class="code">spring-boot-starter</span>YAML
                      </li>
                      <li>
                       <span class="code">spring-boot-starter-test</span>JUnitHamcrestMockito
                      </li>
                    </ul>
                    <pom v-highlight />     
                      <p>
                        Web<span class="code">spring-boot-starter-web</span>
                      </p>
                      <web v-highlight />
                  </div>
                </div>
            </tab-pane>
            <tab-pane label="HelloWorld" icon="md-code">
              <div class="tab-item">
                <h3>HelloWorld</h3>
                <div class="context-box">
                  <ul>
                    <li>
                      
                      <span class="code">package</span>
                      
                      <span class="code">com.example.lesson1</span>
                      
                    </li>
                    <li>
                      
                      <span class="code">HelloController</span>:
                    </li>
                  </ul>
                  <hello-code v-highlight />
                  <ul>
                    <li>
                      
                      <span class="code">http://localhost:8080/hello</span>
                      <span class="code">
                        Hello World
                      </span>
                    </li>
                  </ul>
                </div>
              </div>
            </tab-pane>
            <tab-pane label="" icon="md-code">
              <div class="tab-item">
                  <h3></h3>
                  <div class="context-box">
                    <p>
                      <span class="code">src/test/</span><span class="code">Chapter1ApplicationTests</span>http
                    </p>
                    <test v-highlight />
                    <p>
                      <span class="code">MockServletContext</span><span class="code">WebApplicationContext</span><span class="code">HelloController</span><span class="code">@Before</span><span class="code">MockMvcBuilders.standaloneSetup()</span>
                    </p>
                    <ul>
                      <li>
                        <span class="code">status</span>
                        <span class="code">content</span><span>equalTo</span>
                      </li>
                    </ul>
                    <import-dom v-highlight />
                    <p>
                      MavenSpring Bootweb
                    </p>
                  </div>
              </div>
            </tab-pane>
        </tabs>
    </div>
</template>

<script>
import pom from "./pom.md";
import web from "./web.md";
import helloCode from "./helloCode.md";
import test from "./test.md";
import importDom from "./import.md";
export default {
  data() {
    return {};
  },
  components: {
    pom,
    web,
    helloCode,
    test,
    importDom
  },
  mounted() {}
};
</script>

<style scoped lang='less'>
.first {
  background-color: #fff;
  color: #000;
  padding: 30px 0px;
  h2 {
    font-size: 25px;
    margin-bottom: 20px;
  }
  .tab-item {
    margin: 0 auto;
    margin-top: 30px;
    width: 80%;
    h3 {
      font-size: 20px;
    }
    .img-box {
      margin-top: 20px;
    }
  }
}
</style>


