<style scoped lang='less'>
.hello {
  background: url("../assets/bg.jpg");
  background-size: 100% 100%;
  background-position: 0 0;
  text-align: center;
  overflow: hidden;
  height: 800px;
  background-color: #000;
  color: #fff;
  position: relative;
  .context {
    margin-top: 10%;
  }
  h1 {
    font-size: 50px;
    margin-top: 50px !important;
    font-weight: bold;
    span {
      margin-left: 10px;
      font-size: 20px;
    }
  }
  h2{
    margin-top: 20px;
  } 
  p {
    width: 50%;
    margin: 0 auto;
    font-size: 18px;
    text-indent: 2rem;
    margin-top: 20px;
  }
}
</style>



