import commonView from '@/components';
import RESTfulAPI from "@/components/WebDev/RESTfulAPI.md";
import Thymeleaf from '@/components/WebDev/Thymeleaf.md';
import Swagger2 from '@/components/WebDev/Swagger2.md';
import Abnormity from '@/components/WebDev/Abnormity.md';
import Java8Api from '@/components/WebDev/Java8Api.md';
import XMLResponse from '@/components/WebDev/XMLResponse.md';

export default {
    path: "/Web",
    cnName: 'Web',
    component: commonView,
    name: "Web",
    children: [
      {
        path: 'RESTfulAPI',
        component: RESTfulAPI,
        cnName: "RESTful API",
        title: 'Spring BootRESTful API',
        date: "2018-11-07",
        name: "RESTfulAPI"
      },
      {
        path: "Thymeleaf",
        component: Thymeleaf,
        cnName: "Thymeleaf",
        title: 'Thymeleafweb',
        date: '2018-11-08',
        name: "Thymeleaf"
      },
      {
        path: "Swagger2",
        component: Swagger2,
        cnName: "Swagger2API",
        title: 'Spring BootSwagger2RESTful API',
        date: '2018-11-09',
        name: "Swagger2"
      },
      {
        path: "Abnormity",
        component: Abnormity,
        cnName: "",
        title: 'Spring BootWeb',
        date: '2018-11-10',
        name: "Abnormity"
      },
      {
        path: "Java8Api",
        component: Java8Api,
        title: 'Spring BootFeignJava 8API',
        date: '2018-11-11',
        cnName: 'Java 8API',
        name: "Java8Api"
      },
      {
        path: "XMLResponse",
        component: XMLResponse,
        title: 'Spring BootXML',
        date: '2018-11-12',
        cnName: 'XML',
        name: "XMLResponse"
      }
    ]
  };


