import commonView from '@/components';
import scheduled from "@/components/Async/scheduled.md";
import asyncCall from "@/components/Async/asyncCall.md";
import taskExecutor from "@/components/Async/taskExecutor.md";
import taskClose from "@/components/Async/taskClose.md";
import future from "@/components/Async/future.md";

export default{
    path:'/async',
    cnName:'',
    component:commonView,
    name:"async",
    children:[
        {
            path:'scheduled',
            component:scheduled,
            cnName:'',
            title:'Spring Boot@Scheduled',
            date:'2018-11-16',
            name:'scheduled'
        },
        {
            path:"asyncCall",
            component:asyncCall,
            cnName:"",
            title:"Spring Boot@Async",
            date:"2018-11-17",
            name:"asyncCall"
        },
        {
            path:"taskExecutor",
            component:taskExecutor,
            cnName:"",
            title:"Spring Boot@Async",
            date:"2018-11-18",
            name:"taskExecutor"
        },
        {
            path:"taskClose",
            component:taskClose,
            cnName:"",
            title:"Spring Boot@AsyncThreadPoolTaskScheduler",
            date:"2018-11-19",
            name:"taskClose"
        },
        {
            path:"future",
            component:future,
            cnName:"",
            title:"Spring Boot@AsyncFuture",
            date:"2018-11-20",
            name:"future"
        }
    ]
}


