import commonView from '@/components';
import JdbcTemplate from "@/components/Data/JdbcTemplate.md";
import Jpa from "@/components/Data/Jpa.md";
import MoreDataConfig from "@/components/Data/MoreDataConfig.md";
import Redis from "@/components/Data/Redis.md";
import MongoDB from "@/components/Data/MongoDB.md";
import MyBatis from "@/components/Data/MyBatis.md";
import MyBatisConfig from "@/components/Data/MyBatisConfig.md";
import Flyway from "@/components/Data/Flyway.md";
import LDAP from "@/components/Data/LDAP.md";
import Transaction from "@/components/Data/Transaction.md";

export default {
    path: "/Data",
    cnName: "",
    component: commonView,
    name: "Data",
    children: [
        {
            name: "JdbcTemplate",
            path: "JdbcTemplate",
            cnName: 'JdbcTemplate',
            title: 'Spring BootJdbcTemplate',
            component: JdbcTemplate,
            date: '2018-11-13'
        },
        {
            name: "Jpa",
            path: 'Jpa',
            cnName: "Spring-data-jpa",
            title: "Spring BootSpring-data-jpa",
            component: Jpa,
            date: '2018-11-14'
        },
        {
            name: "MoreDataConfig",
            path: "MoreDataConfig",
            cnName: "",
            title: "Spring Boot",
            component: MoreDataConfig,
            date: '2018-11-14'
        },
        {
            name: "Redis",
            path: "Redis",
            cnName: "NoSQL",
            title: "Spring BootRedis",
            component: Redis,
            date: '2018-11-14'
        },
        {
            name: "MongoDB",
            path: "MongoDB",
            cnName: "NoSQL",
            title: "Spring BootMongoDB",
            component: MongoDB,
            date: '2018-11-14'
        },
        {
            name: "MyBatis",
            path: "MyBatis",
            cnName: "MyBatis",
            title: "Spring BootMyBatis",
            component: MyBatis,
            date: '2018-11-14'
        },
        {
            name: "MyBatisConfig",
            path: "MyBatisConfig",
            cnName: "MyBatis",
            title: "Spring BootMyBatis",
            component: MyBatisConfig,
            date: '2018-11-14'
        },
        {
            name: "Flyway",
            path: "Flyway",
            cnName: "Flyway",
            title: "Spring BootFlyway",
            component: Flyway,
            date: '2018-11-14'
        },
        {
            name: "LDAP",
            path: "LDAP",
            cnName: "LDAP",
            title: "Spring BootLDAP",
            component: LDAP,
            date: '2018-11-14'
        },
        {
            name: "Transaction",
            path: "Transaction",
            cnName: "",
            title: "Spring Boot",
            component: Transaction,
            date: '2018-11-15'
        }
    ]
};


